import React from 'react';
import { createUseStyles } from 'react-jss'
import { Table, TableBody, TableRow, TableCell } from '@material-ui/core'; 

const useStyles = createUseStyles({
    title: {
        color: 'white',
        textAlign: 'center',
    },
    body: {
        margin: '40px',
        fontSize: '24px',
        lineSize: '0px',
    },
    text: {
        margin: '20px',
    },
    link: {
        color: 'cyan'
    },
    table: {
        width: '96% !important',
        margin: '20px',
        color: 'white',
        border: '3px solid white',
        borderRadius: 5,
    },
    cell: {
        color: 'white !important',
        fontSize: '24px',
        background: 'radial-gradient(ellipse at center, #1e579988 0%,#256ec688 0%,#06307a88 100%)',
        padding: 10,
        border: '5px solid white',
        margin: 'auto',
        display: 'table',
    },
    tableContents: {
        margin: '0px',
        fontSize: '20px',
    }
});

export const AboutPage = ({children, url}) => {
    const classes = useStyles();
    return (
        <div className={classes.body}>
            <h2 className={classes.title}>
                About Me
            </h2>
            <h3 className={classes.text}>Hello World! Nice to meet you!</h3>
            <p className={classes.text}>My name is Josh Stephens, also commonly known online under the username, <code>"Jawchewa"</code>.</p>
            <p className={classes.text}>By day, I work as a professional Software Engineer, spending my days building software, web pages and APIs. But at night (and also on weekends), I transform into a hobbyist game developer and game modder.</p>
            <p className={classes.text}>Ever since I was a kid, I was fascinated by computers and video games. I was so intrigued about how they worked that when I was a teenager, I started teaching myself everything I could about programming and game design, so I could build my own games and applications. Since then I've gone through college to level up my skills and obtained a Bachelor's Degree in Sofware and Game Development, and I have gone on to start a career as a Professional Sofware Engineer.</p>
            <h3 className={classes.text}>Skills:</h3>
            <Table className={classes.table}>
                <TableBody>
                    <TableRow>
                        <TableCell className={classes.cell}>
                            <h3 className={classes.tableContents}>Skills:</h3>
                            <ul className={classes.tableContents}>
                                <li>Gameplay Programming</li>
                                <li>UI Programming</li>
                                <li>AI Programming</li>
                                <li>Web Development</li>
                            </ul>
                        </TableCell>
                        <TableCell className={classes.cell}>
                            <h3 className={classes.tableContents}>Programming Languages:</h3>
                            <ul className={classes.tableContents}>
                                <li>C# / .Net</li>
                                <li>C++</li>
                                <li>Javascript</li>
                                <li>React / Redux</li>
                            </ul>
                        </TableCell>
                        <TableCell className={classes.cell}>
                            <h3 className={classes.tableContents}>Development Tools:</h3>
                            <ul className={classes.tableContents}>
                                <li>Unity Game Engine</li>
                                <li>Unreal Game Engine</li>
                                <li>Visual Studio</li>
                                <li>Git</li>
                            </ul>
                        </TableCell>
                    </TableRow>
                </TableBody>
            </Table>
        </div>
    );
}
