import React from 'react';
import {createUseStyles} from 'react-jss'
import Slider from "react-slick";
import projects from './projects.json';

const useStyles = createUseStyles({
    container: {
        color: 'white',
        background: 'radial-gradient(ellipse at center, #1e579933 0%,#256ec633 0%,#06307a33 100%)',
        borderRadius: 5,
        padding: 40,
        paddingTop: 0,
        fontSize: 30,
        border: '5px solid white',
        fontFamily: 'Russo One',
        margin: '10px',
    },
    slideBody: {
        height: 'auto',
        textAlign: 'center',
    },
    title: {
        fontWeight: 'bold',
        textDecoration: 'none',
    },
    link: {
        color: 'white',
        textDecoration: 'none',
        '&:hover': {
            color: 'cyan !important',
        }
    },
    slideImage: {
        width: 'auto',
        height: '540px',
        display: 'inherit !important',
        margin: 'auto'
    }
});

export const GameCarousel = ({children, url}) => {
    const classes = useStyles();
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        draggable: false,
        fade: false,
        autoplay: true,
        autoplaySpeed: 5000,
      };

    return (
        <div className={classes.container}>
            <Slider {...settings}>
                {projects.map(p => (p.previewImage && p.name) ? (
                    <a href={p.url} className={classes.link}>
                        <div className={classes.slideBody}>
                            
                                <p className={classes.title}>{p.name}</p>
                                <img src={p.previewImage} alt={p.icon} className={classes.slideImage}/>
                        </div>
                    </a>
                ) : null)}
            </Slider>
        </div>
    );
}
