import React from 'react';
import { createUseStyles } from 'react-jss';
import { Game } from '../../Components/Game';

const useStyles = createUseStyles({
    title: {
        color: 'white',
        textAlign: 'center'
    },
    body: {
        marginBottom: '40px',
        marginRight: '40px',
        marginLeft: '40px',
        fontSize: '20px',
        width: '70%',
    },
    link: {
        color: 'cyan'
    },
    details: {
        marginTop: '0px',
        marginBottom: '10px',
    },
    columns: {
        display: 'inline-flex',
        marginTop: '40px',
    },
    images: {
        width: '30%',
    },
    image: {
        width: '100%',
        marginTop: '10px'
    },
    video: {
        marginTop: '20px',
        width: '100%',
        height: '200px',
        left: 0,
        right: 0
    }
});

export const TapshotPage = ({children}) => {
    const classes = useStyles();
    return (
        <div>
            <h2 className={classes.title}>
                Tap Shot
            </h2>
            <Game url="/content/games/Tapshot/index.html" alt="/content/images/games/tapshot/Stars.png"/>
            <div className={classes.columns}>
                <div className={classes.body}>
                    <p>Tap Shot is a mobile friendly shoot-em-up that combines classic arcade shooters with modern mobile-friendly design. You control a ship that is cursed so that it must ascend and shoot at the same time every time you tap the screen (Or click space)Take Care of your ship by tapping to avoid asteroids, enemy ships and wormholes. Shoot down these obstacles and collect gems to gain the highest score that you can!</p>
                    <p>This game was made in under 48 hours as part of Miz Jam, Kenney Jam and Extra Credits Jam, because they were all going on at the same time. It was designed for Android, but also supports Windows and can be run in the web browser as well. It was built as sort of a mix of games like Flappy Bird as well as classic arcade shoot-em-ups.</p>
                    <h3 className={classes.details}>Features:</h3>
                    <ul className={classes.details}>
                        <li>Randomly generated layouts to provide endless replayability</li>
                        <li>Mobile Friendly design</li>
                        <li>Simple One Button/Tap control scheme that is easy to learn, but hard to master.</li>
                    </ul>
                    <h3 className={classes.details}>Controls:</h3>
                    <ul className={classes.details}>
                        <li>Tap the screen or press space to both jump and shoot at the same time</li>
                        <li>Before your coin stops moving, tap the screen to unleash your burst ability</li>
                    </ul>
                    <h3 className={classes.details}>Details:</h3>
                    <ul className={classes.details}>
                        <li>Platform: Android, Web Browser, Windows</li>
                        <li>Tools Used: Unity3D, C#</li>
                        <li>Team Size: 1 person</li>
                        <li>Time Frame: 48 hours</li>
                        <li><a className={classes.link} href="/content/games/Windows/TapShot.zip">Windows Download</a></li>
                        <li><a className={classes.link} href="https://jawchewa.itch.io/tap-shot">itch.io Page</a></li>
                        <li><a className={classes.link} href="https://play.google.com/store/apps/details?id=com.Jawchewa.TapShot">Google Play Page</a></li>
                    </ul>
                    <h3 className={classes.details}>Credits:</h3>
                    <ul className={classes.details}>
                        <li>Josh Stephens: Designer and Developer</li>
                    </ul>
                    <h3 className={classes.details}>Additional Resources:</h3>
                    <ul className={classes.details}>
                        <li>Music: OBLIDIVM - <a className={classes.link} href="http://oblidivmmusic.blogspot.com.es/">http://oblidivmmusic.blogspot.com.es</a></li>
                        <li>Art and Fonts: Kenney - <a className={classes.link} href="https://kenney.nl/">https://kenney.nl</a></li>
                    </ul>
                </div>
                <div className={classes.images}>
                    <iframe title="tapshotvideo" frameborder="0" className={classes.video} allowfullscreen="" src="//www.youtube.com/embed/aCCwsZyWfN0"></iframe>
                    <a href='/content/images/games/tapshot/Screenshot1.jpg' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/tapshot/Screenshot1.jpg' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/tapshot/Screenshot2.jpg' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/tapshot/Screenshot2.jpg' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/tapshot/Screenshot3.jpg' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/tapshot/Screenshot3.jpg' alt='' className={classes.image}/>
                    </a>
                </div>
            </div>
        </div>
    );
}
