import React from 'react';
import { createUseStyles } from 'react-jss';
import { Game } from '../../Components/Game';

const useStyles = createUseStyles({
    title: {
        color: 'white',
        textAlign: 'center'
    },
    body: {
        marginBottom: '40px',
        marginRight: '40px',
        marginLeft: '40px',
        fontSize: '20px',
        width: '70%',
    },
    link: {
        color: 'cyan'
    },
    details: {
        marginTop: '0px',
        marginBottom: '10px',
    },
    columns: {
        display: 'inline-flex',
        marginTop: '40px',
    },
    images: {
        width: '30%',
    },
    image: {
        width: '100%',
        marginTop: '10px'
    },
    video: {
        marginTop: '20px',
        width: '100%',
        height: '200px',
        left: 0,
        right: 0
    }
});

export const HoloDefenderPage = ({children}) => {
    const classes = useStyles();
    return (
        <div>
            <h2 className={classes.title}>
                Holo Defender
            </h2>
            <Game url="/content/games/HoloDefender/index.html" alt="/content/images/games/holo-defender/background.png"/>
            <div className={classes.columns}>
                <div className={classes.body}>
                    <p>Holo Defender is an arcade tower defense game originally made for the Microsoft Hololens, and later ported to Windows and web browsers. The goal of this project was to create a simple grid-based tower defense that can be projected into the physical world. It's a standard tower defense, where robot foes move through predetermined paths trying to get to the goal, tp damage damage the player’s home base. Players must use their wits to strategically place a variety of towers onto the map to stop waves of enemies from progressing. However, once the player’s base has taken too much damage, it’s all be over.</p>
                    <p>This game was originally developed a team of four students at Neumont University. This project was made for a class about building Software Products for Emerging Platforms. We were allowed to work on pretty much whatever project we wanted, so long as we made it for a new, emerging platform, so we decided to make a game for the Microsoft Hololens. The main goal was to learn about the platform, and the challenges of developing for it. Unfortunately, we no longer have access to the hardware needed to develop for the hololens, so I later decided to port the project to PC and Web browsers, with a brand new UI, to allow more people to play it.</p>
                    <h3 className={classes.details}>How to play:</h3>
                    <ul className={classes.details}>
                        <li>Click on the tile where you want to place a tower</li>
                        <li>When you're ready, select the tower you want to place(if you have enough money.)</li>
                        <li>When you click on a tower, you can upgrade or sell towers using the buttons on the right</li>
                        <li>Press the pause button at the top to pause the game, where you can quit back to the menu</li>
                        <li>Press the fast forward button to speed up time.</li>
                    </ul>
                    <h3 className={classes.details}>Details:</h3>
                    <ul className={classes.details}>
                        <li>Platform: Windows, Web Browser, Hololens(formerly)</li>
                        <li>Tools Used: Unity3D, C#, Windows SDK</li>
                        <li>Team Size: 4 people</li>
                        <li>Time Frame: 4 weeks + about 3 days for ports</li>
                        <li>Downloads: <a className={classes.link} href="/content/games/Windows/HoloDefender.zip">Windows</a></li>
                    </ul>
                    <h3 className={classes.details}>Credits:</h3>
                    <ul className={classes.details}>
                        <li>Josh Stephens: Lead Game Designer, Gameplay Programmer, UI Programmer</li>
                        <li>Thadius Novak: AI Programmer, Gameplay Programmer</li>
                        <li>Joshua Kauer: Gameplay Programmer</li>
                        <li>Richard Zamora: Level Designer, UI Programmer</li>
                    </ul>
                    <h3 className={classes.details}>Additional Resources:</h3>
                    <ul className={classes.details}>
                        <li><a className={classes.link} href="https://assetstore.unity.com/packages/3d/chibii-robots-66019">Chibii Robots</a> by 3DRT</li>
                        <li><a className={classes.link} href="https://incompetech.com/">Various Songs</a> by Kevin MacLeod</li>
                    </ul>
                </div>
                <div className={classes.images}>
                    <a href='/content/images/games/holo-defender/Old1.jpg' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/holo-defender/Old1.jpg' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/holo-defender/Old3.jpg' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/holo-defender/Old3.jpg' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/holo-defender/Old4.jpg' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/holo-defender/Old4.jpg' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/holo-defender/Old5.jpg' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/holo-defender/Old5.jpg' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/holo-defender/New2.png' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/holo-defender/New2.png' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/holo-defender/New1.png' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/holo-defender/New1.png' alt='' className={classes.image}/>
                    </a>
                </div>
            </div>
        </div>
    );
}
