import React from 'react';
import { createUseStyles } from 'react-jss';
import { Game } from '../../Components/Game';

const useStyles = createUseStyles({
    title: {
        color: 'white',
        textAlign: 'center'
    },
    body: {
        marginBottom: '40px',
        marginRight: '40px',
        marginLeft: '40px',
        fontSize: '20px',
        width: '70%',
    },
    link: {
        color: 'cyan'
    },
    details: {
        marginTop: '0px',
        marginBottom: '10px',
    },
    columns: {
        display: 'inline-flex',
        marginTop: '40px',
    },
    images: {
        width: '30%',
    },
    image: {
        width: '100%',
        marginTop: '10px'
    },
    video: {
        marginTop: '20px',
        width: '100%',
        height: '200px',
        left: 0,
        right: 0
    }
});

export const RoboRepairPage = ({children}) => {
    const classes = useStyles();
    return (
        <div>
            <h2 className={classes.title}>
                Robo Repair
            </h2>
            <Game url="/content/games/RoboRepair/index.html" alt="/content/images/games/robo-repair/robo-repair-background.png"/>
            <div className={classes.columns}>
                <div className={classes.body}>
                    <p>Robo Repair is A 2.5D action platformer about a broken robot's journey to repair himself, and his loved one. You and your significant other are two robots who have crash landed on an alien planet, and your partner has been badly damaged. Travel through an alien factory filled with dangerous robots, while searching for a computer chip that can be used to repair your companion. </p>
                    <p>This game was developed as part of the 2020 Global Game Jam as a Windows game, and I later ported it to Web browser as well. The theme of the jam was "Repair" which is what inspired us to make a love story about a robot tryin to repair their Significant Other.</p>
                    <h3 className={classes.details}>Controls:</h3>
                    <ul className={classes.details}>
                        <li>WASD to move</li>
                        <li>Press Space to Jump</li>
                        <li>Move mouse to aim</li>
                        <li>Click to shoot</li>
                        <li>Escape to Pause and open Crafting/Inventory Menu</li>
                    </ul>
                    <h3 className={classes.details}>Details:</h3>
                    <ul className={classes.details}>
                        <li>Platform: Windows, Web Browser</li>
                        <li>Tools Used: Unity3D, C#, MagicaVoxel, Blender</li>
                        <li>Team Size: 4 people</li>
                        <li>Time Frame: 48 hours initially + half a day for ports and clean up</li>
                        <li>Downloads: <a className={classes.link} href="/content/games/Windows/RoboRepair.zip">Windows</a></li>
                        <li><a className={classes.link} href="https://globalgamejam.org/2020/games/robo-repair-0">Original Global Game Jam Page</a></li>
                    </ul>
                    <h3 className={classes.details}>Credits:</h3>
                    <ul className={classes.details}>
                        <li>Josh Stephens: UI Programmer, Gameplay Programmer, 3D Artist</li>
                        <li>Morgan McClure: Gamplay Programmer</li>
                        <li>Justin Furtado: Gamplay Programmer</li>
                        <li>Joshua Kauer: Gameplay Programmer, Level Design</li>
                    </ul>
                    <h3 className={classes.details}>Additional Resources:</h3>
                    <ul className={classes.details}>
                        <li><a className={classes.link} href="https://opengameart.org/content/8-bit-victory-loop">8-Bit Victory Loop</a> by Wolfgang</li>
                        <li><a className={classes.link} href="https://opengameart.org/content/8-bit-level-music-mug-stage-sewer-pipes-underground">Mug Stage</a> by BossLevelVGM </li>
                        <li><a className={classes.link} href="https://opengameart.org/content/bit-space-ftl-inspired-loop-0">Bit Space</a> by Beam Theory </li>
                        <li><a className={classes.link} href="https://soundcloud.com/gundatsch">Raining Bits</a> by Gundatsch</li>
                        <li><a className={classes.link} href="https://assetstore.unity.com/packages/3d/characters/robots/voxel-robots-86550">Voxel Robots</a> by Moenen</li>
                        <li><a className={classes.link} href="https://assetstore.unity.com/packages/3d/environments/sci-fi/voxel-scifi-environment-free-version-101492">Voxel Scifi Environment</a> by trilobite_mx</li>
                        <li><a className={classes.link} href="https://opengameart.org/content/8-bit-victory-loop">8-Bit Victory Loop</a> by Wolfgang</li>
                    </ul>
                </div>
                <div className={classes.images}>
                    <a href='/content/images/games/robo-repair/Robo1.png' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/robo-repair/Robo1.png' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/robo-repair/Robo3.png' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/robo-repair/Robo3.png' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/robo-repair/Robo7.png' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/robo-repair/Robo7.png' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/robo-repair/Robo2.png' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/robo-repair/Robo2.png' alt='' className={classes.image}/>
                    </a>
                    <a href='/content/images/games/robo-repair/RoboCredits.png' target="_blank" data-image_lightbox="true">
                        <img src='/content/images/games/robo-repair/RoboCredits.png' alt='' className={classes.image}/>
                    </a>
                </div>
            </div>
        </div>
    );
}
