import React from 'react';
import { createUseStyles } from 'react-jss'
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';

const useStyles = createUseStyles({
    footer: {
        fontSize: 16,
        fontFamily: 'Russo One',
        margin: 'auto',
        display: 'table',
        width: '98%',
        marginTop: '10px',
        marginBottom: '10px',
    },
    link: {
        color: 'white',
        textDecoration: 'none',
        padding: '5px',
        '&:hover': {
            color: 'cyan',
        },
    },
    rightAlign: {
        float: "right",
    },
});

export const Footer = ({children}) => {
    const classes = useStyles();
    return (
        <div className={classes.footer}>
            <a className={classes.link} href="https://www.linkedin.com/in/j-stephens/" rel="noopener noreferrer" target="_blank"><LinkedInIcon /></a>
            <a className={classes.link} href="https://twitter.com/Jawchewa" rel="noopener noreferrer" target="_blank"><TwitterIcon /></a>
            <a className={classes.link} href="https://github.com/jawchewa" rel="noopener noreferrer" target="_blank"><GitHubIcon /></a>
            <span className={classes.rightAlign}>Email: <a className={classes.link} href="mailto:jawchewas@gmail.com">jawchewas@gmail.com</a></span>
        </div>
    );
}
