import React from 'react';
import {createUseStyles} from 'react-jss'
import { Button } from '@material-ui/core';

const useStyles = createUseStyles({
    game: {
        margin: '0 auto',
        display: 'table',
    },
    image: {
        backgroundSize: 'cover',
        backgroundPosition: 'center',
    },
    runButton: {
        margin: 'auto !important',
        display: 'flex !important',
        top: '50%',
        fontFamily: 'Russo One !important',
    }
});

export const Game = ({children, url, alt, width="960px", height="640px"}) => {
    const classes = useStyles();
    const [isInGame, setInGame] = React.useState(false);
    return (
        <div className={classes.game}>
            {(!alt || isInGame) && (
                <iframe title='Resume' src={url} width={width} height={height} frameborder="0" allowFullScreen/>
            )}
            {!isInGame && (
                <div className={classes.image} style={{backgroundImage: `url(${alt})`, 
                width: width,
                height: height,
            }}>
                    <Button variant="contained" color="primary" className={classes.runButton} onClick={() => setInGame(true)}>Run Game</Button>
                </div>
            )}
        </div>
    );
}
